import { useState, useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { motion } from 'framer-motion';
import './App.scss';
import { faMoneyBill } from "@fortawesome/free-solid-svg-icons";


function App() {

    const [job, setJobs] = useState([]);


    window.show_player_jobs = (id, name, distance, owner, level, teleport) => setJobs([...job, { id, name, distance, owner, level, teleport }])

    function handle_teleport_click(id) {
        mp.trigger("client::click_teleport_job", id);
    }
    function handle_find_click(id) {
        mp.trigger("client::click_find_job", id);
    }

    useEffect(() => {
        function handleKeyUp(event) { if (event.key === "Escape") mp.trigger("client::destroy_client_job_menu"); }
        window.addEventListener("keyup", handleKeyUp);
        return () => { window.removeEventListener("keyup", handleKeyUp); }
    }, [])

    return (

        <motion.div className="container" initial={{ opacity: 0 }} animate={{ opacity: [0.1, 0.8, 1] }} transition={{ delay: 0.2 }}>
            <div className="header-card">
                <FontAwesomeIcon icon={faMoneyBill} className="icon" />
                <div className="title-section">
                    <p className="title">RAYED.MP</p>
                    <p className="subtitle">JOBS MENU</p>
                </div>
            </div>
            <div className="center-card">
                <div className="links">
                    <div>#.</div>
                    <div>Name</div>
                    <div>Owner</div>
                    <div>Level</div>
                    <div>Distance</div>
                    <div>Options</div>
                </div>
                {job.map((job) => (
                    <div className="lists">
                        <div>{job.id}.</div>
                        <div>{job.name}</div>
                        <div>{job.owner}</div>
                        <div>{job.level}</div>
                        <div>{job.distance}</div>
                        <div className="buttons">
                            <div onClick={() => handle_find_click(job.id)}>Find</div>
                            {job.teleport === true ? <div className="teleport" onClick={() => handle_teleport_click(job.id)}>Teleport</div> : null}
                        </div>
                    </div>
                ))}
            </div>
        </motion.div>
    )
}
export default App;